.project_banner {
    width: 90%;
    margin-bottom: 40px;
    opacity: 0;
    transition: 1.6s;
}

.project_banner.animation {
    opacity: 1;
}

.project_description {
    margin: 30px 0 70px;
    font-size: 13px;
    text-align: left;
    line-height: 160%;
    opacity: 0;
    transform: translate(0, 30px);
    transition: .6s;
}

.project_description.animation {
    opacity: 1;
    transform: translate(0, 0);
}

.projects {
    margin-bottom: 100px;
}

.unit_project {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 60px;
}

.unit_project > .content > .title {
    font-size: 20px;
    font-weight: 700;
    text-align: left;
}

.unit_project > .content > .people {
    margin-top: 6px;
    font-size: 13px;
    text-align: left;
}

.unit_project > .content > .description {
    margin-top: 12px;
    font-size: 13px;
    text-align: left;
    line-height: 160%;
}

.unit_project > img {
    width: 300px;
    height: auto;
    object-fit: contain;
    margin-top: 20px;
}

@media (min-width:992px) {

    .unit_project {
        flex-wrap: nowrap;
    }
    .unit_project > .content > .description {
        font-size: 14px;
    }

    .unit_project > img {
        margin-left: 40px;
        margin-top: 0px;
    }

    .project_description {
        font-size: 16px;
        text-align: justify;
    }
    .project_banner {
        width: 60%;
    }
}